:root {
  --main-font: "open-sans", arial, sans-serif;
  --black: #000000;
  --white: #ffffff;
  --c-text: #444444;
  --c-bold-text: var(--black);
  --c-bgc: #f1f1f1;
  --c-bgc-interior: #63d1bb;
  --c-bgc-toys: #56c3b2;
  --c-bgc-hero: #fafafa;
  --c-line: #ececec;
  --c-link-border: #62d1ba;
  --bgc-rep: no-repeat;
  --bgc-pos: center;
  --bgc-size: auto;
  --border: 1px solid var(--c-line);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
  height: auto;
}

@font-face {
  font-family: "open-sans";
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "open-sans";
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
.page {
  box-sizing: border-box;
  scroll-behavior: smooth;
}

.page-body {
  margin: 0;
  min-width: 320px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-family: var(--main-font);
  font-size: 15px;
  font-weight: 400;
  color: var(--color-text);
  background-color: var(--c-white);
}

.container {
  margin: 0 auto;
  padding-right: 30px;
  padding-left: 30px;
  width: 320px;
}
@media (min-width: 768px) {
  .container {
    padding-right: 50px;
    padding-left: 50px;
    width: 768px;
  }
}
@media (min-width: 1150px) {
  .container {
    width: 1150px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.title {
  margin-top: 0;
  font-size: 25px;
  font-weight: 700;
  line-height: 30px;
  color: var(--c-bold-text);
}
@media (min-width: 768px) {
  .title {
    font-size: 43px;
    line-height: 50px;
  }
}
@media (min-width: 1150px) {
  .title {
    font-size: 53px;
    line-height: 57px;
  }
}

.button {
  text-decoration: none;
  color: inherit;
  display: block;
  padding: 13px 15px;
  text-align: center;
  text-transform: uppercase;
  font-family: var(--main-font);
  font-weight: 700;
  font-size: 18px;
  line-height: 20px;
  border: 2px solid var(--c-text);
}

.header {
  position: relative;
}
.header::before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  height: 79px;
  background: var(--white);
  border-bottom: var(--border);
  z-index: 3;
}
@media (min-width: 768px) {
  .header {
    background: var(--c-bgc-hero);
  }
  .header::before {
    height: 88px;
    z-index: 2;
  }
}
@media (min-width: 768px) {
  .header--catalog, .header--form {
    background: var(--c-bgc);
  }
}
@media (min-width: 1150px) {
  .header--catalog, .header--form {
    background: var(--c-bgc);
  }
}

.header__container {
  display: grid;
  grid-template-columns: 1fr;
  padding-top: 21px;
  padding-bottom: 22px;
}
@media (min-width: 768px) {
  .header__container {
    position: relative;
    display: block;
    padding-top: 0;
    padding-bottom: 0;
    border: none;
    background: var(--c-bgc-hero);
  }
}
@media (min-width: 768px) {
  .header__container--catalog, .header__container--form {
    background: var(--c-bgc);
  }
}
@media (min-width: 1150px) {
  .header__container--catalog, .header__container--form {
    background: var(--c-bgc);
  }
}

.header__link-logo {
  position: relative;
  z-index: 3;
  grid-column: 1/-1;
  grid-row: 1/2;
  align-self: center;
  justify-self: center;
}
@media (min-width: 768px) {
  .header__link-logo {
    position: absolute;
    top: 21px;
    left: 50px;
    z-index: 3;
  }
}
@media (min-width: 1150px) {
  .header__link-logo {
    position: absolute;
    top: 10px;
    left: 50%;
    transform: translateX(-50%);
  }
}

.header__burger {
  position: relative;
  z-index: 3;
  grid-column: 1/-1;
  grid-row: 1/2;
  align-self: center;
  justify-self: end;
}
@media (min-width: 768px) {
  .header__burger {
    display: none;
  }
}

.link-logo {
  display: block;
}

.link-logo__img {
  display: block;
}
@media (min-width: 768px) {
  .link-logo__img {
    width: 114px;
    height: 46px;
  }
}
@media (min-width: 1150px) {
  .link-logo__img {
    width: 149px;
    height: 72px;
  }
}

.navigation {
  position: relative;
  padding-top: 20px;
  z-index: 2;
  background-color: var(--white);
}
@media (min-width: 768px) {
  .navigation {
    padding-top: 0;
    background-color: transparent;
  }
}

.navigation__js-active {
  position: absolute;
  left: 0;
  right: 0;
  top: 100%;
  padding-top: 0;
  visibility: hidden;
  transform: translateY(-150%);
  transition: visibility 0.4s, transform 0.4s cubic-bezier(0, 0.76, 0.74, 0.92);
}
@media (min-width: 768px) {
  .navigation__js-active {
    position: relative;
    visibility: visible;
    transform: none;
  }
}

.navigation--active {
  visibility: visible;
  transform: none;
}

.navigation__list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .navigation__list {
    display: grid;
    grid-template-columns: 25% 1fr 264px;
    grid-template-rows: minmax(88px, 1fr);
  }
}
@media (min-width: 1150px) {
  .navigation__list {
    grid-template-columns: 21% 1fr 264px;
  }
}

.navigation__item {
  border-top: var(--border);
  padding-top: 17px;
  padding-bottom: 17px;
}
@media (min-width: 768px) {
  .navigation__item {
    display: flex;
    align-items: center;
    border-top: none;
  }
}

@media (min-width: 768px) {
  .navigation__item--search {
    grid-column: 2/3;
    grid-row: 1/2;
    padding-top: 0;
    padding-bottom: 0;
    justify-self: end;
    align-self: center;
    border-left: var(--border);
    border-right: var(--border);
  }
}

@media (min-width: 768px) {
  .navigation__item--cart {
    grid-column: 3/4;
    grid-row: 1/2;
    justify-self: end;
    padding-left: 12px;
    padding-top: 0;
    padding-bottom: 0;
  }
}

@media (min-width: 768px) {
  .navigation__item--catalog {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
@media (min-width: 1150px) {
  .navigation__item--catalog {
    grid-column: 1/2;
    grid-row: 1/2;
    width: 184px;
    border-right: var(--border);
  }
}

@media (min-width: 768px) {
  .navigation__item--order {
    grid-column: 2/3;
    grid-row: 2/3;
  }
}
@media (min-width: 1150px) {
  .navigation__item--order {
    grid-column: 2/3;
    grid-row: 1/2;
  }
}

.navigation__item--delivery {
  display: none;
}
@media (min-width: 768px) {
  .navigation__item--delivery {
    display: block;
    grid-column: 3/4;
    grid-row: 2/3;
    justify-self: end;
  }
}

.navigation__item--new {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__item--new {
    display: block;
  }
}

.navigation__item--sale {
  display: none;
}
@media (min-width: 1150px) {
  .navigation__item--sale {
    display: block;
  }
}

.navigation__link {
  text-decoration: none;
  color: inherit;
  display: block;
  padding-top: 10px;
  padding-right: 15px;
  padding-bottom: 10px;
  padding-left: 46px;
  margin-right: auto;
  margin-left: auto;
  width: 260px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  background: var(--bgi) var(--bgc-rep) left center/18px 18px;
}
@media (min-width: 768px) {
  .navigation__link {
    padding-top: 5px;
    padding-right: 0;
    padding-bottom: 5px;
    padding-left: 0;
    margin-right: 0;
    margin-left: 0;
    width: auto;
  }
}
.navigation__link--search {
  --bgi: url("../img/sprite.svg#search");
}
@media (min-width: 768px) {
  .navigation__link--search {
    min-height: 88px;
    width: 82px;
    background-position: center;
  }
}
.navigation__link--cart {
  --bgi: url("../img/sprite.svg#cart");
  background-size: 21px 18px;
}
@media (min-width: 768px) {
  .navigation__link--cart {
    padding-left: 52px;
    justify-self: end;
  }
}

.navigation__link--new,
.navigation__link--sale {
  padding-top: 4px;
  padding-right: 15px;
  font-size: 15px;
}

@media (min-width: 1150px) {
  .navigation__link--sale {
    max-width: 190px;
  }
}

@media (min-width: 768px) {
  .navigation__link--catalog,
.navigation__link--order {
    padding-right: 15px;
    font-size: 15px;
  }
}
@media (min-width: 1150px) {
  .navigation__link--catalog,
.navigation__link--order {
    font-size: 17px;
  }
}

@media (min-width: 1150px) {
  .navigation__link--order {
    max-width: 190px;
  }
}

.navigation__link--delivery {
  display: none;
}
@media (min-width: 768px) {
  .navigation__link--delivery {
    display: block;
    padding-left: 15px;
    font-size: 15px;
    font-weight: 400;
  }
}
@media (min-width: 1150px) {
  .navigation__link--delivery {
    padding-top: 4px;
  }
}

@media (min-width: 768px) {
  .navigation__link-wrap {
    display: none;
  }
}

.burger {
  background-color: transparent;
  cursor: pointer;
  width: 20px;
  height: 14px;
  overflow: hidden;
  border: none;
  background: url("../img/sprite.svg#burger") var(--bgc-rep) var(--bgc-pos)/var(--bgc-size);
}

.burger--active {
  width: 21px;
  height: 20px;
  background-image: url("../img/sprite.svg#burger-close");
}

.footer {
  padding-top: 28px;
  padding-bottom: 32px;
  background-color: var(--c-bgc);
}
@media (min-width: 768px) {
  .footer {
    padding-top: 49px;
    padding-bottom: 58px;
  }
}

@media (min-width: 768px) {
  .footer__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }
}

.footer__link-logo {
  display: none;
}
@media (min-width: 768px) {
  .footer__link-logo {
    display: block;
  }
}

.footer__social {
  padding-bottom: 30px;
  border-bottom: 1px solid #e5e5e5;
}
@media (min-width: 768px) {
  .footer__social {
    padding-bottom: 0;
    border-bottom: none;
  }
}

.footer__developer {
  display: flex;
  align-items: center;
  justify-content: center;
}

.footer__span-developer {
  padding-top: 30px;
  padding-right: 27px;
  font-weight: 700;
  font-size: 19px;
  line-height: 18px;
  color: var(--color-bold-text);
}
@media (min-width: 768px) {
  .footer__span-developer {
    padding-top: 0;
  }
}

.footer__link-developer {
  display: inline-block;
  width: 27px;
  height: 35px;
  background: url("../img/sprite.svg#html-academy") var(--bgc-rep) var(--bgc-pos)/27px 35px;
  transform: translateY(15px);
}
@media (min-width: 768px) {
  .footer__link-developer {
    transform: none;
  }
}

.social-list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
  display: flex;
  justify-content: center;
  gap: 29px;
}

.social-list__link {
  display: block;
  width: 28px;
  height: 22px;
  background: var(--bgi) var(--bgc-rep) var(--bgc-pos)/var(--bgc-size);
}
.social-list__link--vk {
  --bgi: url("../img/sprite.svg#vkontakte");
}
.social-list__link--youtube {
  --bgi: url("../img/sprite.svg#youtube");
}
.social-list__link--telegram {
  --bgi: url("../img/sprite.svg#telegram");
}

.main {
  flex-grow: 1;
}

.hero {
  position: relative;
  padding-top: 73px;
  background: var(--c-bgc-hero) url("../img/hero-mobile@1x.png") var(--bgc-rep) var(--bgc-pos) -18px/var(--bgc-size);
  overflow-x: hidden;
}
@media (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-mobile@2x.png");
    background-size: 320px 373px;
  }
}
@media (min-width: 768px) {
  .hero {
    padding-top: 88px;
    background-image: url("../img/hero-tablet@1x.png");
    background-position: var(--bgc-pos) -68px;
  }
}
@media (min-width: 768px) and (min-resolution: 2dppx) {
  .hero {
    background-image: url("../img/hero-tablet@2x.png");
    background-size: 768px auto;
  }
}
@media (min-width: 768px) {
  .hero::after {
    background: url("../img/sprite.svg#icon-zigzag-fill-tablet") repeat-x center bottom/768px 24px;
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 26px;
    z-index: 1;
  }
}
@media (min-width: 1150px) {
  .hero {
    padding-top: 92px;
  }
  .hero::after {
    background: url("../img/zigzag-fill-desktop.png") repeat-x center bottom/1128px;
    height: 34px;
  }
}

.hero__container {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 416px;
}
@media (min-width: 768px) {
  .hero__container {
    min-height: 605px;
  }
}
@media (min-width: 1150px) {
  .hero__container {
    min-height: 605px;
  }
}

.hero__title {
  margin-bottom: 0;
  padding-bottom: 30px;
  text-align: center;
  font-size: 27px;
  align-self: center;
}
@media (min-width: 768px) {
  .hero__title {
    max-width: 650px;
    font-size: 43px;
  }
}
@media (min-width: 1150px) {
  .hero__title {
    max-width: 700px;
    font-size: 53px;
    line-height: 57px;
  }
}

.hero__list {
  margin: 0;
  padding-left: 0;
  list-style-type: none;
}
@media (min-width: 768px) {
  .hero__list {
    display: flex;
    flex-wrap: wrap;
  }
}

@media (min-width: 768px) {
  .hero__item {
    width: 50%;
  }
}

.hero__link {
  text-decoration: none;
  color: inherit;
  position: relative;
  display: block;
  padding: 24px 110px 31px 0;
  min-height: 103px;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  color: var(--white);
  z-index: 1;
}
.hero__link::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  width: 100vw;
  z-index: -1;
}
@media (min-width: 768px) {
  .hero__link::before {
    left: 0;
    right: 0;
    transform: none;
    width: auto;
  }
}
.hero__link--interior::before {
  background: var(--c-bgc-interior) url("../img/sprite.svg#hero-interior") var(--bgc-rep) left calc(50% + 105px) top 24px/40px 62px;
}
@media (min-width: 768px) {
  .hero__link--interior::before {
    background-position: right 37px top 30px;
  }
}
.hero__link--toys::before {
  background: var(--c-bgc-toys) url("../img/sprite.svg#hero-toy") var(--bgc-rep) left calc(50% + 105px) top 23px/60px 60px;
}
@media (min-width: 768px) {
  .hero__link--toys::before {
    background-position: right 33px top 31px;
  }
}
@media (min-width: 768px) {
  .hero__link {
    font-size: 20px;
    line-height: 30px;
    padding: 26px 140px 48px 36px;
  }
}
@media (min-width: 1150px) {
  .hero__link {
    padding: 25px 325px 50px 36px;
  }
}

.featured {
  background: url("../img/sprite.svg#icon-zigzag-mobile") repeat-x center bottom/320px 6px;
  padding-top: 33px;
  padding-bottom: 43px;
}
@media (min-width: 768px) {
  .featured {
    background: url("../img/sprite.svg#icon-zigzag-tablet") repeat-x center bottom/768px 17px;
    padding-top: 36px;
    padding-bottom: 68px;
  }
}
@media (min-width: 1150px) {
  .featured {
    padding-top: 52px;
    padding-bottom: 0;
    background-image: none;
  }
}

@media (min-width: 768px) {
  .featured__container {
    display: grid;
    grid-template-columns: 1fr 50%;
    column-gap: 42px;
  }
}
@media (min-width: 1150px) {
  .featured__container {
    background: url("../img/sprite.svg#icon-zigzag-desktop") no-repeat center bottom/1050px 23px;
    grid-template-columns: 133px 1fr 50%;
    padding-bottom: 95px;
  }
}

.featured__title {
  margin-top: 0;
  margin-bottom: 4px;
}
@media (min-width: 768px) {
  .featured__title {
    margin-bottom: 37px;
    grid-column: 1/3;
    grid-row: 1/2;
  }
}
@media (min-width: 1150px) {
  .featured__title {
    position: relative;
    grid-column: 2/4;
    grid-row: 1/2;
    margin-bottom: 33px;
    padding-top: 62px;
  }
}

.featured__marking {
  margin-top: 0;
  margin-bottom: 17px;
  font-weight: 700;
  font-size: 17px;
  line-height: 20px;
  text-transform: uppercase;
  color: var(--c-bgc-interior);
}
@media (min-width: 768px) {
  .featured__marking {
    grid-column: 1/3;
    grid-row: 1/2;
    justify-self: end;
    padding-top: 6px;
    margin-right: 24px;
    width: 80px;
    line-height: 17px;
    text-align: center;
  }
}
@media (min-width: 1150px) {
  .featured__marking {
    grid-column: 1/2;
    grid-row: 1/2;
    justify-self: end;
    align-self: center;
    margin-right: 12px;
    margin-bottom: 0;
    padding-top: 17px;
  }
}

.featured__text {
  margin-top: 0;
  margin-bottom: 21px;
  line-height: 24px;
}
@media (min-width: 768px) {
  .featured__text {
    grid-column: 1/2;
    grid-row: 2/3;
    margin-bottom: 41px;
    font-size: 17px;
    line-height: 30px;
  }
}
@media (min-width: 1150px) {
  .featured__text {
    grid-column: 2/3;
    grid-row: 2/3;
    margin-bottom: 26px;
    min-height: 112px;
  }
}

.featured__list {
  margin-top: 0;
  margin-bottom: 28px;
  line-height: 20px;
}
@media (min-width: 768px) {
  .featured__list {
    grid-column: 1/2;
    grid-row: 3/4;
    margin-bottom: 58px;
  }
}
@media (min-width: 1150px) {
  .featured__list {
    grid-column: 2/3;
    grid-row: 3/4;
    min-height: 160px;
    margin-bottom: 67px;
  }
}

.featured__item {
  display: grid;
  grid-template-columns: 38% 1fr;
  padding-top: 7px;
  padding-bottom: 12px;
  border-bottom: 1px solid var(--c-line);
}
@media (min-width: 768px) {
  .featured__item {
    grid-template-columns: 33% 1fr;
  }
}
@media (min-width: 1150px) {
  .featured__item {
    grid-template-columns: 32% 1fr;
  }
}

.featured__property {
  grid-column: 1/2;
}

.featured__description {
  margin: 0;
  grid-column: 2/3;
}

.featured__image-wrap {
  margin-bottom: 27px;
}
@media (min-width: 768px) {
  .featured__image-wrap {
    grid-column: 2/3;
    grid-row: 2/6;
    margin-bottom: 0;
  }
}
@media (min-width: 1150px) {
  .featured__image-wrap {
    grid-column: 3/4;
    grid-row: 1/6;
  }
}

.featured__image {
  display: block;
}
@media (min-width: 768px) {
  .featured__image {
    width: 334px;
    height: 100%;
  }
}
@media (min-width: 1150px) {
  .featured__image {
    width: 525px;
    height: auto;
  }
}

.featured__price {
  margin-top: 0;
  margin-bottom: 24px;
  font-weight: 700;
  font-size: 17px;
  line-height: 24px;
  text-align: center;
